var redux_actions_1 = require('redux-actions');
var Firebase = require('firebase');
var ActionTypes_1 = require('../constants/ActionTypes');
var main_1 = require('../main');
var dbRoot = new Firebase('https://mhfi-poker.firebaseio.com/');
var sessions = dbRoot.child('sessions');
var users = dbRoot.child('users');
function handleAuth(authData) {
    var _a = authData[authData.provider], displayName = _a.displayName, profileImageURL = _a.profileImageURL;
    main_1.store.dispatch({
        type: ActionTypes_1.UserAction.Auth,
        payload: {
            userId: authData.uid,
            name: displayName,
            avatarUrl: profileImageURL
        }
    });
}
setTimeout(function () {
    var auth = dbRoot.getAuth();
    if (auth) {
        handleAuth(auth);
    }
    else {
        dbRoot.authWithOAuthPopup('google', function (error, authData) {
            if (error) {
                console.error('Authentication Error', error);
            }
            else {
                //console.log('Authenticated: ', authData);
                handleAuth(authData);
            }
        });
    }
}, 10);
sessions.on('value', function (snapshot) {
    var value = snapshot.val();
    main_1.store.dispatch({
        type: ActionTypes_1.SessionAction.ListChange,
        payload: value ? Object.keys(value).map(function (k) { return value[k]; }) : []
    });
});
var editSession = redux_actions_1.createAction(ActionTypes_1.SessionAction.Change, function (sessionId, name, desc) {
    var session = {
        sessionId: sessionId, name: name, desc: desc,
        lastUpdated: new Date()
    };
    sessions.child(sessionId).update(session);
    return session;
});
exports.editSession = editSession;
var removeSession = redux_actions_1.createAction(ActionTypes_1.SessionAction.Remove, function (sessionId) {
    sessions.child(sessionId).remove();
    return sessionId;
});
exports.removeSession = removeSession;
var joinSession = function (sessionId, user) {
    return function (dispatch) {
        var sessionUsers = users.child(sessionId);
        var userData = (_a = {},
            _a[user.userId] = user.toJS(),
            _a
        );
        sessionUsers.update(userData);
        sessions.child(sessionId).once('value', function (snapshot) {
            dispatch({
                type: ActionTypes_1.SessionAction.Join,
                payload: snapshot.val()
            });
        });
        sessions.child(sessionId).on('value', function (snapshot) {
            dispatch({
                type: ActionTypes_1.SessionAction.Change,
                payload: snapshot.val()
            });
        });
        sessionUsers.on('value', function (snapshot) {
            var value = snapshot.val();
            dispatch({
                type: ActionTypes_1.UserAction.ListChange,
                payload: value ? Object.keys(value).map(function (k) { return value[k]; }) : []
            });
        });
        window.onbeforeunload = function (e) {
            sessionUsers.child(user.userId).remove();
        };
        var _a;
    };
};
exports.joinSession = joinSession;
var addSession = function (name, desc, user) {
    return function (dispatch) {
        var newRef = sessions.push();
        var session = {
            sessionId: newRef.key(),
            name: name, desc: desc,
            lastUpdated: new Date(),
            adminUser: user.userId
        };
        newRef.set(session, function (error) {
            if (error) {
                console.error(error);
            }
            else {
                joinSession(session.sessionId, user)(dispatch);
            }
        });
    };
};
exports.addSession = addSession;
var leaveSession = redux_actions_1.createAction(ActionTypes_1.SessionAction.Leave, function (sessionId, userId) {
    var sessionUsers = users.child(sessionId);
    // not sure if this will work when sessionUsers is not the same instance as .on()
    sessionUsers.off('value');
    sessions.child(sessionId).off('value');
    window.onbeforeunload = null;
    sessionUsers.child(userId).remove();
    return sessionId;
});
exports.leaveSession = leaveSession;
var setVote = function (sessionId, userId, vote) {
    return function (dispatch) {
        users.child(sessionId + "/" + userId)
            .update({ vote: vote });
        dispatch({
            type: ActionTypes_1.VoteAction.Set,
            payload: { userId: userId, vote: vote }
        });
    };
};
exports.setVote = setVote;
var clearVotes = function (sessionId, userId) {
    return function (dispatch) {
        // TODO
    };
};
exports.clearVotes = clearVotes;



