/// <reference path="../../typings/tsd.d.ts" />
var Immutable = require('immutable');
var defaultUser = {
    userId: '', name: '',
    avatarUrl: '', vote: null
};
/// Instantiating this constructor generates an immutable User record wrapper.
exports.UserRecord = Immutable.Record(defaultUser, "User");
var defaultSession = {
    sessionId: '', name: '', desc: '',
    adminUser: '',
    lastUpdated: new Date()
};
/// Instantiating this constructor generates an immutable Session record wrapper.
exports.SessionRecord = Immutable.Record(defaultSession, "Session");
var defaultAppState = {
    currentUser: new exports.UserRecord(),
    currentSession: new exports.SessionRecord(),
    sessionNames: Immutable.List([]),
    users: Immutable.List([])
};
exports.AppRecord = Immutable.Record(defaultAppState, "App");
/// Valid story points. Abstain = -1.
exports.StoryPoints = Immutable.List([-1, 1, 2, 3, 5, 8, 13, 20, 40, 100]);



